#navbarSupportedContent{
    overflow:hidden;
    position:relative;
}
#navbarSupportedContent ul{
    padding:0px;
    margin:0px;
}
#navbarSupportedContent ul li a i{
    margin-right:10px;
}
#navbarSupportedContent li{
    list-style-type:none;
    float:left;
    color:grey;
}
#navbarSupportedContent ul li a{
    color:white;
    text-decoration:none;
    font-size:15px;
    display:block;
    padding:20px 20px;
    transition-duration:0.6s;
    transition-timing-function:cubic-bezier(0.68,-0.55,0.26s,1.5s);
    position:relative;
}
#navbarSupportedContent >ul >li.active>a{
    color:black;
    background-color:transparent; 
    transition:all 0.7s;
} 


    #navbarSupportedContent a:not(:only-child):after{
    content:'\f105';
    position:absolute;
    right:20px;
    top:10px;
    font-size:14px;
    font-family:'Font Awesome 5 Free';
    display:inline-block;
    padding-right:3px;
    vertical-align:middle;
    font-weight:900;
    transition:0.5s;
}
#navbarSupportedContent .active > a:not(:only-child):after{
    transition:rotate(90deg);
} 
.hori-selector{
    display:inline-block;
    position:absolute;
    height:100%;
    top:0px;
    left:0px;
    transition-duration:0.6s;
    transition-timing-function:
    cubic-bezier(0.68,-0.5s,0.265,1.5s);
    background-color:#fff;
    border-top-left-radius:15px;
    border-top-right-radius:15px;
    margin-top: 10px;
}
.hori-selector .right,
.hori-selector .left{
    position:absolute;
    width:25px;
    height:25px;
    background-color: #fff;
    bottom:10px;
}
.hori-selector .right{
    right:-25px;
}
.hori-selector .left{
    left:-25px;
}
.hori-selector .right:before,
.hori-selector .left:before{
    content:'';
    position:absolute;
    width:50px;
    height:50px;
    border-radius: 50px;
    background-color: #494643;
}
.hori-selector .right:before{
    bottom:0;
    right:-25px;
}
.hori-selector .left:before{
    bottom:0;
    left:-25px;
}
@media (max-width:991px){
    #navbarSupportedContent ul li a{
        padding:12px 30px;
    }
    .hori-selector{
        margin-top:0px;
        margin-left:10px;
        border-radius: 0px;
        border-top-left-radius:25px;
        border-bottom-left-radius:25px;
    }
    .hori-selector .left,.hori-selector .right{
        right:10px;
    }
    .hori-selector .left{
        top:-25px;
        left:auto;
    }
    .hori-selector .right{
        bottom:-25px;
    }
    .hori-selector .left:before{
        left:-25px;
        top:-25px;
        
    }
    .hori-selector .right:before{
        bottom:-25px;
        left:-25px;
    }
    .fa-cannabis{
        margin-left:-500px;
    }

}
@media (max-width:730px){
    .fa-cannabis{
        margin-left:0px;
    }
}
